@Page("/splash")
@Composable
fun Splash(){
    val ctx = rememberPageContext()
    LaunchedEffect(Unit){
        delay(3500)
        ctx.router.navigateTo(Routes.login)
    }
Box (
    modifier = Modifier
            .fillMaxSize()
            .backgroundImage(
                linearGradient(
                    eparishColors.secondary, eparishColors.third
                )
            ),
        contentAlignment = Alignment.Center
    ){
    Column (
        verticalArrangement = Arrangement.spacedBy(8.px),
        horizontalAlignment = Alignment.CenterHorizontally
    ){
        //setting the logo
        Img(src = "logo.png", alt = "logo", attrs = Modifier
            .borderRadius(50.percent)
            .size(200.px)
            .toAttrs()
        )
        //CircularProgress(50)
    }


        H5(
            attrs = Modifier
                .align(Alignment.BottomCenter)
                .color(eparishColors.primary)
                .fontWeight(
                FontWeight.Bold
            )
                .padding(topBottom = 8.px)
                .toAttrs()
        ) {
            Text("Developed by lennycodes")

        }
    }
}