package org.example.eparish.pages

import androidx.compose.runtime.*
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.Overflow
import com.varabyte.kobweb.compose.css.functions.linearGradient
import com.varabyte.kobweb.compose.foundation.layout.*
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.icons.fa.*
import com.varabyte.kobweb.silk.components.icons.fa.IconSize
import com.varabyte.kobweb.silk.style.CssStyle
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.selectors.active
import com.varabyte.kobweb.silk.style.selectors.hover
import com.varabyte.kobweb.silk.style.toModifier
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.example.eparish.ResultDataText
import org.example.eparish.components.ResultHeaderText
import org.example.eparish.components.catholics
import org.example.eparish.components.eparishColors
import org.example.eparish.components.tableCell
import org.jetbrains.compose.web.attributes.InputType
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.*



@OptIn(DelicateApi::class)
@Composable
fun Catholics() {
    val breakpoint = rememberBreakpoint()
    val isLargeScreen = breakpoint >= Breakpoint.LG
    var sajiliMkatoliki by remember { mutableStateOf(false) }

    // Search state
    var searchText by remember { mutableStateOf("") }

    // Filtered list
    val filteredCatholics = catholics.filter { item ->
        item.fullName.contains(searchText, ignoreCase = true) ||
                item.nambaYaUbatizo.contains(searchText, ignoreCase = true)
    }

    Column(
        modifier = Modifier
            .fillMaxWidth()
            .borderRadius(12.px)
            .padding(16.px)
            .color(eparishColors.primary)
    ) {

        if (!sajiliMkatoliki) {

            /** ---------------------------
             *   Top Bar + Add Button
             * ---------------------------- */
            Row(
                modifier = Modifier.fillMaxWidth()
                    .fontSize(22.px)
                    .fontWeight(FontWeight.Bold)
                    .color(eparishColors.text),
                horizontalArrangement = Arrangement.Start,
                verticalAlignment = Alignment.CenterVertically
            ) {
                Text("Orodha ya Wakatoliki")
                Span(attrs = Modifier.weight(1f).toAttrs())

                Button(
                    modifier = buttonStyle.toModifier(),
                    onClick = { sajiliMkatoliki = true }
                ) {
                    Row(
                        verticalAlignment = Alignment.CenterVertically,
                        horizontalArrangement = Arrangement.spacedBy(6.px)
                    ) {
                        FaPlus(size = IconSize.XL, modifier = Modifier.color(eparishColors.text))
                        if (isLargeScreen) Text("Sajili Mkatoliki")
                    }
                }
            }

            /** ---------------------------
             *   Search Bar
             * ---------------------------- */
            Row(
                modifier = Modifier
                    .fillMaxWidth()
                    .margin(top = 16.px, bottom = 10.px),
                verticalAlignment = Alignment.CenterVertically
            ) {
                Input(
                    type = InputType.Text,
                    attrs = Modifier
                        .fillMaxWidth()
                        .padding(8.px)
                        .borderRadius(12.px)
                        .fontSize(18.px)
                        .backgroundColor(Color.white)
                        .padding(leftRight = 12.px, topBottom = 8.px)
                        .toAttrs {
                            onInput { event -> searchText = event.value }
                            attr("placeholder", "Tafuta kwa Jina au Namba ya Ubatizo...")
                        }
                )
            }

            /** ---------------------------
             * Desktop Table Header
             * ---------------------------- */
            if (isLargeScreen) {
                Row(
                    modifier = Modifier
                        .padding(4.px)
                        .fillMaxWidth()
                        .backgroundImage(linearGradient(eparishColors.primary, eparishColors.secondary))
                ) {
                    ResultHeaderText("S/N", 0.08f)
                    ResultHeaderText("Namba ya Ubatizo", 0.18f)
                    ResultHeaderText("Jina Kamili", 0.44f)
                }
            }

            /** ---------------------------
             * List Items (Scrollable Container)
             * ---------------------------- */
            Column(
                modifier = Modifier
                    .fillMaxWidth()
                    .height(300.px)             // Fixed height
                    .overflow(Overflow.Auto)    // Enable vertical scrolling
                    .padding(right = 4.px)
            ) {

                filteredCatholics.forEachIndexed { index, catholic ->

                    if (isLargeScreen) {
                        // Desktop Row
                        val bgColor = if (index % 2 == 0)
                            eparishColors.third.copy(alpha = 0.15f.toInt())
                        else eparishColors.primary

                        Row(
                            modifier = desktopRowStyle.toModifier()
                                .backgroundColor(bgColor)
                                .fillMaxWidth()
                                .padding(4.px),
                            verticalAlignment = Alignment.CenterVertically
                        ) {
                            ResultDataText((index + 1).toString(), weight = 0.08f)
                            ResultDataText(catholic.nambaYaUbatizo, weight = 0.18f)
                            ResultDataText(catholic.fullName, weight = 0.44f)

                            Row(
                                modifier = Modifier
                                    .flexGrow(0.2f)
                                    .flexBasis(0.percent)
                                    .then(tableCell),
                                horizontalArrangement = Arrangement.Center,
                                verticalAlignment = Alignment.CenterVertically
                            ) {
                                Button(
                                    modifier = endeleaButtonStyle
                                        .toModifier(),
                                    onClick = {}
                                ) {
                                    FaTrashCan(size = IconSize.XL, modifier = Modifier.color(eparishColors.red))
                                }
                                Span(attrs = Modifier.size(8.px).toAttrs())
                                Button(
                                    modifier = endeleaButtonStyle.toModifier(),
                                    onClick = {}
                                ) {
                                    FaUserPen(size = IconSize.XL, modifier = Modifier.color(eparishColors.primary))
                                }
                            }
                        }


                    } else {
                        // Mobile Card
                        Column(
                            modifier = Modifier
                                .fillMaxWidth()
                                .padding(topBottom = 6.px)
                                .backgroundColor(
                                    if (index % 2 == 0) eparishColors.third.copy(alpha = 0.1f.toInt()) else Color.white
                                )
                                .borderRadius(8.px)
                                .padding(12.px)
                        ) {
                            ResultDataText(catholic.fullName, weight = 1f)
                            Row(
                                modifier = Modifier.fillMaxWidth(),
                                horizontalArrangement = Arrangement.Start
                            ) {
                                ResultDataText("Ubatizo: ${catholic.nambaYaUbatizo}", weight = 1f)
                            }

                            Row(
                                modifier = Modifier.fillMaxWidth().padding(top = 8.px),
                                horizontalArrangement = Arrangement.spacedBy(10.px)
                            ) {
                                Button(
                                    modifier = endeleaButtonStyle.toModifier(),
                                    onClick = {}
                                ) {
                                    FaTrashCan(size = IconSize.XL, modifier = Modifier.color(eparishColors.red))
                                }
                                Button(
                                    modifier = endeleaButtonStyle.toModifier(),
                                    onClick = {}
                                ) {
                                    FaUserPen(size = IconSize.XL, modifier = Modifier.color(eparishColors.primary))
                                }
                            }
                        }
                    }

                }
            }

        } else {
            Registration()
        }
    }
}

/** ------------------------------
 * BUTTON STYLES
------------------------------- */
val buttonStyle = CssStyle {
    base {
        Modifier
            .borderRadius(12.px)
            .color(eparishColors.text)
            .padding(8.px)
            .backgroundImage(linearGradient(eparishColors.primary, eparishColors.third))
    }
    hover { Modifier.boxShadow(offsetX = 0.px, offsetY = 3.px, blurRadius = 10.px, color = Color.black) }
}

val endeleaButtonStyle = CssStyle {
    base {
        Modifier
            .borderRadius(12.px)
            .padding(8.px)
            .backgroundColor(Color.white)
    }
    hover { Modifier.backgroundColor(eparishColors.third.copy(alpha = 0.3f.toInt())) }
}

val desktopRowStyle = CssStyle {
    base {
        Modifier
            .padding(4.px)
            .fillMaxWidth()
            .borderRadius(6.px)
            .padding(6.px)
    }
    hover { Modifier.backgroundColor(eparishColors.third.copy(alpha = 0.3f.toInt())) }
}

// --- Back / Sitisha Button ---
val sitishaButtonStyle = CssStyle {
    base {
        Modifier
            .borderRadius(12.px)
            .color(eparishColors.text)
            .padding(topBottom = 8.px, leftRight = 12.px)
            .backgroundColor(Color.black)
            .boxShadow(
                offsetX = 0.px,
                offsetY = 3.px,
                blurRadius = 10.px,
                color = eparishColors.text
            )
    }
    hover {
        Modifier.boxShadow(
            offsetX = 0.px,
            offsetY = 3.px,
            blurRadius = 10.px,
            color = Color.gray
        )
    }
    active {
        Modifier.boxShadow(
            offsetX = 0.px,
            offsetY = 3.px,
            blurRadius = 10.px,
            color = eparishColors.primary
        )
    }
}
